import { Block } from '../../../utils/block/block';
import { Button } from '../../../components/buttons/button-mixin';
import { Input } from '../../../components/inputs/input-mixin';
import { Link } from '../../../components/links/link-mixin';
import { validateBlock } from '../../../utils/validator/validator';
import { serializer } from '../../../utils/serializer/serializer';
import AuthController from '../../../controllers/AuthController';
import template from './signUp.pug';
import './signUp.scss';

interface SignUpProps {
  title: string;
}

export class SignUp extends Block {
  constructor(props: SignUpProps) {
    super(props);
  }

  init() {
    this.children.email = new Input({
      name: 'email',
      type: 'text',
      label: '',
      placeholder: '',
      error: ' ',
      events: {
        focusout: (event: Event) => validateBlock(event),
        focusin: (event: Event) => validateBlock(event),
      },
    });

    this.children.login = new Input({
      name: 'login',
      type: 'text',
      label: '',
      placeholder: '',
      error: '     ',
      events: {
        focusout: (event: Event) => validateBlock(event),
        focusin: (event: Event) => validateBlock(event),
      },
    });

    this.children.firstName = new Input({
      name: 'first_name',
      type: 'text',
      label: '',
      placeholder: '',
      error: '     ',
      events: {
        focusout: (event: Event) => validateBlock(event),
        focusin: (event: Event) => validateBlock(event),
      },
    });

    this.children.secondName = new Input({
      name: 'second_name',
      type: 'text',
      label: '',
      placeholder: '',
      error: '     ',
      events: {
        focusout: (event: Event) => validateBlock(event),
        focusin: (event: Event) => validateBlock(event),
      },
    });

    this.children.phone = new Input({
      name: 'phone',
      type: 'text',
      label: '',
      placeholder: '',
      error: '     ',
      events: {
        focusout: (event: Event) => validateBlock(event),
        focusin: (event: Event) => validateBlock(event),
      },
    });

    this.children.password = new Input({
      name: 'password',
      type: 'password',
      label: '',
      placeholder: '',
      error: '      8 ',
      events: {
        focusout: (event: Event) => validateBlock(event),
        focusin: (event: Event) => validateBlock(event),
      },
    });

    this.children.repeatPassword = new Input({
      name: 'repeatPassword',
      type: 'password',
      label: ' ( )',
      placeholder: '',
      error: '  ',
      events: {
        focusout: (event: Event) => validateBlock(event),
        focusin: (event: Event) => validateBlock(event),
      },
    });

    this.children.button = new Button({
      type: 'submit',
      content: '',
      btnClass: 'sign-up',
      events: {
        click: (e) => this.onSubmit(e),
      },
    });

    this.children.link = new Link({
      content: '',
      to: '/messanger',
    });
  }

  onSubmit(e) {
    e.preventDefault();
    const fields = [
      'email',
      'login',
      'first_name',
      'second_name',
      'phone',
      'password',
      'repeatPassword',
    ];
    serializer(e, fields);
    const dataForm = serializer(e, fields);
    AuthController.signup(dataForm);
  }

  render() {
    return this.compile(template, this.props);
  }
}
